* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  word-break: break-all; }

body {
  overflow-x: hidden; }

.Demo {
  margin: 10px 0; }
  .Demo .Demo-head {
    width: 100%;
    margin: 10px; }
  .Demo .Demo-input {
    width: 50%;
    float: left;
    padding: 10px; }
    .Demo .Demo-input .Demo-input-text {
      border: 1px solid #eee;
      padding: 5px; }
  .Demo .Demo-output {
    width: 50%;
    float: right;
    padding: 10px; }
    .Demo .Demo-output .Demo-output-markdown {
      border: 1px solid #eee;
      padding: 5px; }

.Markdown {
  color: #212121; }
  .Markdown .Toc {
    display: inline-block;
    border: 1px solid #eaeaea;
    padding: 20px; }
    .Markdown .Toc a {
      text-decoration: none; }
  .Markdown h1, .Markdown h2 {
    margin-top: 24px;
    margin-bottom: 16px;
    padding-bottom: 8px;
    border-bottom: 1px solid #eaecef; }
  .Markdown h1 {
    font-size: 30px;
    font-weight: 500; }
  .Markdown h2 {
    font-size: 26px;
    font-weight: 500; }
  .Markdown h3 {
    margin-top: 24px;
    margin-bottom: 12px;
    padding-bottom: 6px;
    border-bottom: 1px solid #eaecef;
    font-size: 22px;
    font-weight: 500; }
  .Markdown ol, .Markdown ul {
    padding-left: 25px; }
  .Markdown blockquote {
    border-left: 5px solid #eee;
    padding-left: 20px;
    margin: 30px; }
  .Markdown table {
    border-spacing: 0;
    border-collapse: collapse;
    margin-bottom: 16px; }
    .Markdown table thead {
      background-color: #f6f8fa; }
      .Markdown table thead td {
        text-align: center;
        font-weight: 600; }
    .Markdown table td {
      padding: 10px 15px;
      border: 1px solid #dfe2e5; }
  .Markdown img {
    max-width: 100%; }
  .Markdown video {
    max-width: 100%; }
  .Markdown pre {
    padding: 15px;
    overflow-x: auto; }
    .Markdown pre.md-code-simple {
      background-color: #F5F5F5; }
    .Markdown pre code {
      line-height: 1.3; }
      .Markdown pre code.md-code-simple {
        background-color: #F5F5F5; }
  .Markdown code {
    font-family: Consolas,Monaco,Lucida Console,Liberation Mono,DejaVu Sans Mono,Bitstream Vera Sans Mono,Courier New; }
    .Markdown code.md-inlinecode {
      background-color: #F5F5F5; }



